.wrapper {
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  
    @media screen and (min-width: 480px) {
      max-width: 480px;
    }
  
    @media screen and (min-width: 768px) {
      max-width: 768px;
    }
  
    @media screen and (min-width: 1320px) {
      max-width: 1320px;
    }
  }